                            font-size: 28px;
                            margin-top: 10px; 
                            margin-bottom: 10px; 
                            max-width: 100%;
                            min-height: 1em; 
                            line-height: 2em; 
                            box-sizing: border-box !important; 
                            word-wrap: break-word !important;
                        }
