<template>
    <div class="refuelingAuditing">
        <div class="wrap">
            <p class="text">{{refuelingDetail.deviceNumber}}</p>
            <p class="text">{{refuelingDetail.content}}</p>
            <p class="text">{{refuelingDetail.date}}</p>
            <p class="text">{{refuelingDetail.remark}}</p>
            <div class="imgBox">
                <img :src="refuelingDetail.imgUrl">
            </div>
            <div class="btnBox">
                <mt-button type="primary" @click.native="confirm"></mt-button>
                <mt-button type="danger" @click.native="objection"></mt-button>
            </div>
        </div>
    </div>
</template>

<script>
import zc from "../assets/images/zc.jpg";

export default {
    data() {
        return {
            refuelingDetail:{
                deviceNumber:"Z01",
                content:"Z01100L",
                date:"2018315 153120",
                remark:"",
                imgUrl:zc
            }
        };
    },
    created() {
    },
    mounted() {
    },
    methods: {
        confirm() {
            console.log("!");
        },
        objection() {
            console.log("!");
        }
    }
};
</script>

<style scoped lang="less">
.refuelingAuditing{
    .wrap{
        padding: 30px;
        .text{
            height: 50px;
            font-size: 28px;
        }
        .imgBox{
            text-align: center;
            img{
                display: inline-block;
                margin-top: 50px;
                width: 80%;
            }
        }
        .btnBox{
            margin-top: 50px;
            text-align: center;
        }
    }
}
</style>



